import React from 'react';
import {Avatar, Grid, Paper, TextField, Button, Typography,Link } from '@material-ui/core'
// import LockIcon from '@mui/icons-material/Lock';
import AddCircleOutlineIcon from '@mui/icons-material/AddCircleOutline';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import { useForm } from 'react-hook-form';
import './login.css'
import axios from 'axios';




export default function SignUp({handleChange}) {

    let {  register, handleSubmit } = useForm();

    let onSubmisssion = (data)=>{

        axios.post('/signup', data);

        console.log(data);
    }

    let paperStyle = {padding:20, height: '70vh', width: 280 , margin: "20px auto" }
    let avatarStyle = {backgroundColor: '#1bbd7e'}
    let btnStyle = {margin: '15px 0'}
    let passStyle ={margin: '10px 0'}
  return <div>
       <Grid>
          <Paper elevation={10} style= {paperStyle}>
              <Grid align="center">
              <Avatar style={avatarStyle}><AddCircleOutlineIcon/></Avatar>
              <h2>Sign Up</h2>
              <h4>Join Us by Signing Up</h4>
              </Grid>

              <form onSubmit={handleSubmit(onSubmisssion)}>


              <TextField type="text" placeholder="User Name"
              {...register('username')}
               fullWidth required/>

              <TextField type="email" placeholder=" Email"
              {...register('email')}
               fullWidth style={passStyle} required/>

              <TextField type="number" placeholder="Phone Number"
              {...register('phone')}
               fullWidth style={passStyle} required/>
             
              <TextField type="password" placeholder="Enter Password"
              {...register('password')}
              fullWidth required/>

              <FormControlLabel control={<Checkbox  />} 
              label="I accept terms and conditions" />

              <button className='loginBtn' style={btnStyle} color='primary' 
              variant="contained" fullWidth >Sign Up</button>

              </form>

<Typography style={btnStyle}> Already have an account?
              <Link href = "#" onClick={()=>handleChange("event", 0)}>
               Log In
              </Link>
          </Typography>
          </Paper> 
      </Grid>
      
  </div>;
}
