import React from 'react';
import {Avatar, Grid, Paper, TextField, Button, Typography,Link, Input } from '@material-ui/core'
import LockIcon from '@mui/icons-material/Lock';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import { useForm } from 'react-hook-form';
import './login.css'
import axios from 'axios';
import { Navigate, useNavigate} from 'react-router-dom';


export default function LogIn({handleChange}) {

    let {  register, handleSubmit } = useForm();

    let onSubmisssion = (data)=>{

        axios.post('/login',data).then(function(resp){
            if(resp.data){
                // Navigate('/home');
                alert("User found") 
            } else{
                alert("User Not Found")
            }
        })
        console.log(data);
    }


    let paperStyle = {padding:20, height: '70vh', width: 280 , margin: "20px auto" }
    let avatarStyle = {backgroundColor: '#1bbd7e'}
    let btnStyle = {margin: '15px 0'}
    let passStyle ={margin: '20px 0'}
    

   
  return <div>
      <Grid>
          <Paper elevation={10} style= {paperStyle}>
              <Grid align="center">
              <Avatar style={avatarStyle}><LockIcon/></Avatar>
              <h2>Sign In</h2>
              </Grid>

              <form onSubmit={handleSubmit(onSubmisssion)}>

              <TextField placeholder="User Name"
              {...register('username')}
               fullWidth required/>

              <TextField style={passStyle} placeholder="Password"
              {...register('password')}
              
               type="password" fullWidth required/>
              <FormControlLabel control={<Checkbox />} label="Remember Me" />
              <button className='loginBtn' style={btnStyle}  color='primary' variant="contained"
               fullWidth >Log In</button>

               </form>
          <Typography>
              <Link href = "#">Forget Password?</Link>
          </Typography>

          <Typography style={btnStyle}> Don't have an account?
              <Link href = "#" onClick={()=>handleChange("event",1)}>Sign Up</Link>
          </Typography>

          </Paper> 
      </Grid>
  </div>;
}
